import { useEffect, useState } from 'react';
import { getOphan } from '../client/ophan/ophan';
export const usePageViewId = (renderingTarget) => {
    const [id, setId] = useState();
    useEffect(() => {
        getOphan(renderingTarget)
            .then(({ pageViewId }) => {
            setId(pageViewId);
        })
            .catch(() => {
            setId('no-page-view-id-available');
        });
    }, [renderingTarget]);
    return id;
};
